export const productsReducer = (state, action) => {
  switch (action.type) {
    case "GET-PRODUCTS":
      return { ...state, fetchedProducts: action.payload ,cloneProducts:action.payload};

    case "GET-Error":
      return {
        ...state,
        error: action.payload,
      };

    case "ADD-TO-CART":
      return {
        ...state,
        cart: [...state.cart, action.payload],
      };
case"CHANGE_CATEGORY":
let target=state.fetchedProducts.filter(data=>{
return data.category===action.payload
})
return{...state,cloneProducts:target}
    default:
      return state;
  }
};
